import React from 'react';
import { compose, withProps, withHandlers } from 'recompose';

import { observer } from 'mobx-react';
import Tab from '@material-ui/core/Tab';
import Paper from '@material-ui/core/Paper';
import Snackbar from '@material-ui/core/Snackbar';
import { withStyles } from '@material-ui/core/styles';
import messagesId from 'translation/messagesId';
import DeleteIcon from 'components/DeleteIcon';
import ShowLayer from 'components/ShowLayer';

import bem from 'bem-cn';
// import logic from './logic';
// import logic from './logic';
import styles from './styles';
import './style.css';

const bemBlock = bem('mosaic-capture');

class MosaicCapture extends React.Component {
  state = {
    show: false,
    animating: false,
    capture: null
  };

  static getDerivedStateFromProps(nextProps, prevState) {
    const {
      mosaic: { mosaicCapture, mosaicCaptureNotEmpty }
    } = nextProps;

    if (mosaicCapture !== prevState.capture && mosaicCaptureNotEmpty !== prevState.show) {
      return { show: mosaicCaptureNotEmpty, capture: mosaicCapture };
    }
    return null;
  }

  hideCaptureHandler = () => {
    this.setState({ show: false });
  };

  onAnimatingEnd = () => {
    this.setState({ animating: false });
  };

  render() {
    const { onAnimatingEnd, hideCaptureHandler } = this;
    const { show } = this.state;
    const {
      mosaic: { mosaicCapture, mosaicConfig }
    } = this.props;

    const bemMods = { show };

    const anchorOrigin = {
      vertical: 'top',
      horizontal: 'right'
    };

    return (
      <Snackbar
        classes={{ root: bemBlock(bemMods) }}
        anchorOrigin={anchorOrigin}
        open={show}
        onClose={hideCaptureHandler}
      >
        <Paper onTransitionEnd={onAnimatingEnd}>
          <div>
            {mosaicConfig.cfg}
            123
          </div>
          <div className={bemBlock('capture-wrapper')}>
            <img className={bemBlock('capture')} src={mosaicCapture} alt="capture" />
          </div>
        </Paper>
      </Snackbar>
    );
  }
}
// const EnhancedLayerItem = logic(withStyles(styles)(LayerItem));
const EnhancedMosaicCapture = compose(
  withStyles(styles),
  observer
)(MosaicCapture);

export default EnhancedMosaicCapture;
